import React, { useState, useCallback, useEffect } from 'react';
import style from "./right.module.less";
import utils from '../../../../utils';
import { Button, Space, Toast, Pagination, Spin } from "@douyinfe/semi-ui";
import {
    //   IconPlayCircle,
    IconPlus,
    IconForward,
    IconDownload,
    IconComment,
    IconClock,
} from "@douyinfe/semi-icons";
import TableItem from "../playListDetail/TableItem";
import Commit from "../../../../components/Commit";
import CommitList from "../../../../components/CommitList/CommitList";
import { comment, likeComment } from "../../../../services/comment";
import { getPlayListCommit } from "../../../../services/apis";

export default function RightContent(props) {
    const [comments, setComments] = useState([]); // è¯„è®ºåˆ—è¡¨
    const [hotComments, setHotComments] = useState([]); // çƒ­é—¨è¯„è®ºåˆ—è¡¨
    const [page, setPage] = useState(1); // è¯„è®ºåˆ—è¡¨
    const [limit, setLimit] = useState(20); // é¡µå®¹é‡
    const [total, setTotal] = useState(0); // æ€»è¯„è®ºæ•°
    const [loading, setLoading] = useState(false); // loading
    const {detail, id, text} = props;

    // æäº¤è¯„è®º
    const playListCommit = useCallback(async (val) => {
        const res = await comment({
            t: 1,
            type: 2,
            id: id,
            content: val,
            timestamp: Date.now(),
        });
        if (res.code === 200) {
            Toast.success({
                content: "è¯„è®ºæˆåŠŸ",
                duration: 2,
            });
            getComments();
        }
    }, []);
    // é¡µç å˜åŒ–
    const handlePageChange = useCallback((page) => {
        setPage(page);
    }, []);
    // é¡µå®¹é‡å˜åŒ–
    const handleLimitChange = useCallback((limit) => {
        setLimit(limit);
    }, []);
    const getComments = async () => {
        setLoading(true);
        const res = await getPlayListCommit({
            id: id,
            limit,
            offset: (page - 1) * limit,
            timestamp: Date.now(),
        });
        if (res.code === 200) {
            setTotal(res.total);
            setComments(res.comments);
            setHotComments(res.hotComments);
            setLoading(false);
        }
    }
    // è·å–è¯„è®ºåˆ—è¡¨
    useEffect(() => {
        getComments();
        return () => { };
    }, [id, limit, page]);

    // ç‚¹èµè¯„è®º
    const like = useCallback(
        async (cid, liked) => {
            const res = await likeComment({
                id,
                cid,
                t: liked ? 0 : 1,
                type: 2,
                timestamp: Date.now(),
            });
            if (res.code === 200) {
                if (liked) {
                    Toast.success({
                        content: "å–æ¶ˆèµæˆåŠŸ",
                        duration: 2,
                    })
                } else {
                    Toast.success({
                        content: "èµæˆåŠŸ",
                        duration: 2,
                    })
                }
                getComments();
            } else {
                Toast.error({
                    content: "æ“ä½œå¤±è´¥"
                })
            }
        },
        [],
    );

    // åˆ é™¤è¯„è®º
    const del = async (cid) => {
        const res = await comment({
            t: 0,
            type: 2,
            id: id,
            commentId: cid,
            timestamp: Date.now(),
        });
        if (res.code === 200) {
            Toast.success({
                content: "åˆ é™¤æˆåŠŸ",
                duration: 2,
            });
            getComments();
        }
    }
    return (
        <div className={style['right-content']}>
            <div className="top-content">
                <div className="img">
                    <img src={detail.coverImgUrl} alt="" />
                </div>
                <div className="list-content">
                    <div className="list-name">{detail.name}</div>
                    <div className="create-desc">
                        <IconClock />
                        æœ€è¿‘æ›´æ–°ï¼š
                        {utils.formatDate(detail.updateTime)}
                        ï¼ˆ{text}ï¼‰
                    </div>
                    <div className="operates">
                        <Space>
                            {/* <Button icon={<IconPlayCircle />}>æ’­æ”¾</Button> */}
                            <Button type="tertiary" icon={<IconPlus />}>
                                ({detail.subscribedCount})
                            </Button>
                            <Button type="tertiary" icon={<IconForward />}>
                                ({detail.shareCount})
                            </Button>
                            <Button type="tertiary" icon={<IconDownload />}>
                                ä¸‹è½½
                            </Button>
                            <Button type="tertiary" icon={<IconComment />} onClick={() => {
                                utils.goAnchor("#comment");
                            }}>
                                ({detail.commentCount})
                            </Button>
                        </Space>
                    </div>
                </div>
            </div>
            <div className="music-list">
                <div className="list-header">
                    <h3 className="title">æ­Œæ›²åˆ—è¡¨</h3>
                    <span className="total">{detail.trackCount}é¦–æ­Œ</span>
                    <span className="play-num">
                        æ’­æ”¾ï¼š<span className="num">{detail.playCount}</span>æ¬¡
                    </span>
                </div>
                <ul className="list-wrap">
                    <li className="table-header">
                        <div className="play-num"></div>
                        <div className="title bd">æ ‡é¢˜</div>
                        <div className="duration bd">æ—¶é•¿</div>
                        <div className="name bd">æ­Œæ‰‹</div>
                    </li>
                    {detail.tracks.map((t, i) => (
                        <TableItem showAlbum={false} key={t.id} index={i + 1} item={t} />
                    ))}
                </ul>
            </div>
            <Commit
                commitNum={detail.commentCount}
                commit={playListCommit}
                commitLength={140}
            />
            <CommitList like={like} del={del} total={total} comments={comments} hotComments={hotComments} />
            {total > 0 ? (
                <div className="pagination-wrapper">
                    <Pagination
                        total={total}
                        currentPage={page}
                        onPageChange={handlePageChange}
                        showSizeChanger
                        pageSize={limit}
                        pageSizeOpts={[20, 30, 50]}
                        onPageSizeChange={handleLimitChange}
                    ></Pagination>
                </div>
            ) : null}
            <Spin
                spinning={loading}
                tip="loading..."
                size="large"
                style={{
                    position: "fixed",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    zIndex: "9999",
                }}
            ></Spin>
        </div>
    )
}
